// remJS

// iphone 6
var clientWidth = 375;		/*  */
var fontSize = 20;			/* HTML */

// 
window.onload = function(){
	// html
	var html = document.documentElement;

	// 
	var nowClientWidth = html.clientWidth;

	// htmlfont-size
	var newFontSize = nowClientWidth*fontSize/clientWidth;


	// htmlfontSize
	html.style.fontSize = newFontSize+'px';
}


