<template>
  <div id="search">
    <div class="header">
    <div class="header-wrap">
      <div class="close">
        <a @click="returnback" href="#"></a>
      </div>
      <div class="input">
        <input type="text" class="search">
        <img :src="require('@/assets//imgs/search2.png')" alt="">
      </div>
    </div>
  </div class="header">
  <div class="content">
    <ul class="list">
      <li><router-link to="/movie"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="/book"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="/group"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
      <li><router-link to="#"><strong>FM</strong><span></span></router-link></li>
      <li><router-link to="#"><strong></strong><span></span></router-link></li>
    </ul>
    <div class="opration">
      <div>
        <a class="add" href="http://accounts.douban.com/register?redir=https://m.douban.com/movie/"></a>
      </div>
      <div>
        <a href="http://accounts.douban.com/login?redir=https://m.douban.com/movie/"></a>
      </div>
      <div>
        <a class="add" href="/to_pc/?url=https%3A%2F%2Fm.douban.com%2Fmovie%2F"></a>
      </div>
      <div>
        <a href="#">App</a>
      </div>
    </div>
  </div>
  </div>
</template>  
<script>
  export default {
    methods: {
      returnback () {
        this.$router.back()
      }
    }
  }
</script>
<style>
  .header{
    width: 100%;
    background:#fff;
    border-bottom:0.05rem solid #f3f3f3;
    position:fixed;
    top:0;
    left:0;
    padding:0.75rem 0;
  }
  .header-wrap{
    width:90.4%;
    height:1.55rem;
    height:1.55rem;
  }
  .close{
    float:left;
    width:1.6rem;
    height:0.95rem;
    line-height:0.95rem;
    font-size:0.75rem;
    text-align:center;
  }
  .close>a{
    color:#42db56;
  }
  .input{
    width:14.75rem;
    float:right;
    margin-top:-0.25rem;
    position:relative;
  }
  .search{
    width:14.75rem;
    height:1.45rem;
    padding:0.05rem;
    border-radius:0.2rem;
    background:#f3f3f3;
    background-size:0.867rem;
  }
  .input>img{
    width:0.867rem;
    position:absolute;
    top:0.375rem;
    left:50%;
  }
  /**/
  .content{
    width:90.4%;
    height:100%;
    margin:0 auto;
    margin-top:3.05rem;
  }
  .list{
    padding-top:1.75rem;
    width:100%;
    overflow:hidden;  
    height:auto;  
  }
  .list>li{
    width:5.5rem;
    text-align: center;
    height:2.25rem;
    margin-bottom:1.25rem;
    float:left;
  }
  .list>li>a{
    display:block;
  }
  .list>li>a>strong{
    font-weight:400;
    display:block;
    font-size:1.2rem;
    line-height:1.4rem;
  }
  .list>li:first-child>a>strong{
    color:rgb(35, 132, 232);
  }
  .list>li:nth-of-type(2)>a>strong{
    color:rgb(230, 70, 126);
  }
  .list>li:nth-of-type(3)>a>strong{
    color:rgb(159, 120, 96);
  }
  .list>li:nth-of-type(4)>a>strong{
    color:rgb(225, 100, 77);
  }
  .list>li:nth-of-type(5)>a>strong{
    color:rgb(122, 106, 219);
  }
  .list>li:nth-of-type(6)>a>strong{
    color:rgb(42, 184, 204);
  }
  .list>li:nth-of-type(7)>a>strong{
    color:rgb(87, 116, 197);
  }
  .list>li:nth-of-type(8)>a>strong{
    color:rgb(64, 207, 169);
  }
  .list>li:nth-of-type(9)>a>strong{
    color:rgb(159, 120, 96);
  }
  .list>li:nth-of-type(10)>a>strong{
    color:rgb(244, 143, 46);
  }
  .list>li:nth-of-type(11)>a>strong{
    color:rgb(89, 108, 221);
  }
  .list>li:nth-of-type(12)>a>strong{
    color:rgb(66, 189, 86);
  }
  .list>li>a>span{
    display:block;
    color:#999;
    font-size:0.6rem;
    line-height:0.85rem;
  }
  /**/
  .opration{
    width:100%;
    height:auto;
    overflow:hidden;
    border-bottom:0.05rem solid #f3f3f3;
  }
  .opration>div{
    width:50%;
    float:left;
    height: 0.85rem;
    line-height:;
    padding:0.65rem 0;
    text-align: center;
    font-size:0.75rem;
    border-top:0.05rem solid #f3f3f3;
  }
  .opration>div>a{
    display:block;
    color:#42bd56;
  }
  .opration>div>a.add{
    border-right:0.05rem solid #f3f3f3;
  }
</style>


